* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  justify-content: center;
  align-items: center;
  display: flex;
  height: 100vh;
}
.calculator {
  background-color: cadetblue;
  padding: 10px;
  display: grid;
  grid-template-columns: repeat(4, 50px);
  border: solid;
  border-radius: 10px;
  padding-left: 20px;
  padding-right: 20px;
}
input {
  background-color: white;
  color: black;
  font-size: large;
  grid-column: span 4;
  width: 200px;
  height: 50px;
  margin-top: 40px;
  margin-bottom: 30px;
  text-align: RIGHT;
  border-radius: 10px;
}
button {
  height: 40px;
  width: 40px;
  border-radius: 20px;
  margin: 10px;
}
