.card {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 350px;
  width: 100%;
  background-color: white;
  color: black;
  padding: 20px;
  border-radius: 10px;
}
body {
  background-color: yellow;
}
.card h2 {
  font-family: cursive;
  font-size: large;
  text-align: center;
  text-transform: uppercase;
}
.input-text {
  margin-bottom: 10px;
}
.input-text input {
  width: 100%;
  border: solid 1px black;
}
.input-text textarea {
  width: 100%;
  height: 80px;
  resize: none;
}
.button {
  padding: 10px;
  text-align: center;
  font-family: cursive;
  font-size: large;
  color: black;
}
.card #error_message {
  background-color: crimson;
  padding: 10px;
  margin-bottom: 20px;
  transition: all 0.5s ease;
  width: 100%;
  text-align: center;
  font-size: 14px;
  color: white;
}
